import * as React from "react";
import styled from "styled-components";

const StyledCard = styled.div.attrs({
  className: 'pa2 br2 mw-5 bg-white bs-default'
})`
  width: 250px;
  margin: 0 auto;
`

export default class Card extends React.Component<any, any> {
  render() {
    return (
      <StyledCard>
        {this.props.children}
      </StyledCard>
    );
  }
}